import { useRef, useState } from "react";
import classes from "./MealItemForm.module.css";
import Input from "../../UI/Input";

const MealItemForm = (props) => {
  const [amountIsValid, setAmountIsValid] = useState(true);
  const amountInputRef = useRef();
  const MAXIMUM_ORDER_LIMIT = 5
  const MINIMUM_ORDER_LIMIT = 1
  const TRIMMED_LENGTH = 0
  const submitHandler = (event) => {
    event.preventDefault();

    const enteredAmount = amountInputRef.current.value;
    const enteredAmountNumber = +enteredAmount;

    if (enteredAmount.trim().length === TRIMMED_LENGTH ||
    enteredAmountNumber < MINIMUM_ORDER_LIMIT ||
    enteredAmountNumber > MAXIMUM_ORDER_LIMIT
    ) {
      setAmountIsValid(false);
      return;
    }

    props.onAddToCart(enteredAmountNumber);
  };

  return (
    <form className={classes.form} onSubmit={submitHandler}>
      <Input
        ref={amountInputRef}
        label="Količina"
        input={{
          id: "amount_" + props.id,
          type: "number",
          min: MINIMUM_ORDER_LIMIT,
          max: MAXIMUM_ORDER_LIMIT,
          step: "1",
          defaultValue: "1",
        }}
      />
      <button>Dodaj</button>
      {!amountIsValid && <p>Molimo Vas da unesete moguću količinu! (1-5) </p>}
    </form>
  );
};

export default MealItemForm;
